
export class PlayerData {
    constructor(){
        this.socketID = null;
        this.signText = "";
    }

    setSocketID(value){
        this.socketID = value;
    } 

    getSocketID(){
        return this.socketID;
    }

    setSignText(value){
        this.signText = value;
    }

    getSignText(){
        return this.signText;
    }
}