let Helper = require('Helper');

cc.Class({
    extends: cc.Component,

    properties: {
        editBox:cc.EditBox,
        signLabel: cc.Label
    },

    start () {

    },

    onCreateSignHandler(){
        this.signLabel.string = this.editBox.string;
    },

    onDoneHandler(){
        Helper.createSign = this.signLabel.string;
        cc.director.loadScene("game");
    }


});
