let CamAdjust = cc.Class({
    extends: cc.Component,

    properties:{
        cameras: [cc.Camera], 
        vx:0,
        vy:0, 
        ax:0.1,
        maxV:0.3,

        rotX:0,
        rotY:0,

        rotRangeX: 1,
        rotRangeY: 3,

        screenWidth: 0,
        designResolutionHeight_2:0

    },

    onLoad: function(){
        this.screenWidth = cc.view.getDesignResolutionSize().height / cc.view.getCanvasSize().height * cc.view.getCanvasSize().width;
        this.designResolutionHeight_2 = cc.view.getDesignResolutionSize().height/2;
        this.rotX =  this.cameras[0].node.eulerAngles.x;
        this.rotY =  this.cameras[0].node.eulerAngles.y;
        console.log(this.rotX, this.rotY);

        this.node.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.on(cc.Node.EventType.MOUSE_MOVE, this.onMouseMove, this);
     },
 
     onDestroy: function(){
        this.node.off(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.on(cc.Node.EventType.MOUSE_MOVE, this.onMouseMove, this);
     },

     onTouchMove: function(evt){
         console.log(evt);
     },

     onMouseMove: function(evt){ 
         let targetY = (this.screenWidth/2 - evt._x)/ (this.screenWidth/2) * this.rotRangeY + this.rotY;
         let targetX = ( evt._y - this.designResolutionHeight_2 ) / this.designResolutionHeight_2 * this.rotRangeX + this.rotX;

         //console.log("target", targetX, targetY);
         for(let i = 0; i < this.cameras.length; i++){
            let rotate3DTo = cc.rotate3DTo(1, cc.v3(targetX, targetY, 0));
           
            this.cameras[i].node.stopAllActions();
            this.cameras[i].node.runAction(rotate3DTo);
         }
        
    }
});