

let PlatformTouch = cc.Class({
    extends: cc.Component,

    properties:{
        camera: cc.Camera, 
        platformRoot: cc.Node
    },

    statics:{
        PLATFORM_GROUND_Y: 224,
        instance: null
    },

    onLoad(){
        cc.director.getPhysics3DManager().enabled = true;
        this.node.on(cc.Node.EventType.TOUCH_END, this.onTouch, this);
        PlatformTouch.instance = this;
      },

      onTouch: function(event){
        let ray = this.camera.getRay(event.getLocation());
        let maxDistance = 2000;
        const results = cc.director.getPhysics3DManager().raycastClosest(ray, "3d", maxDistance);
       // console.log(results);
        if(results && results.hitPoint.y == PlatformTouch.PLATFORM_GROUND_Y){
           // console.log(results.name, "click on ground");

            //let pos = (this.platformRoot.convertToNodeSpaceAR(cc.Vec3( results.hitPoint.x,  results.hitPoint.y,  results.hitPoint.z)));
            this.node.emit("click_on_ground", results.hitPoint);
        }
      }, 
})