let Server = require("Server");
let PlayerBase = require("PlayerBase");

cc.Class({
    extends: PlayerBase,

    properties:{
        isSpawn: false,
        moveTween: null
    },

    initialize: function (info){
        this.playerData.setSignText(info.sign);
        this.updateSignLabel();
        this.moveTo(info.pos);
    },

    moveTo: function(info){
        if(info== null){
            return;
        }
        if(!this.isSpawn){
            this.isSpawn = true;
            this.node.x = info.x;
            this.node.y = info.y;
            this.node.z = info.z;
            this.node.zIndex = this.node.z;
        }
        else{
            this.node.stopAllActions();
            this.playWalk();
            let oldx = this.node.x;
            if(info.x > oldx){
                this.anim.node.scaleX = 1;
                this.updateSignScale(1);
            }
            else if(info.x < oldx){
                this.anim.node.scaleX = -1;
                this.updateSignScale(-1);
            }
            
        }

        if(this.moveTween){
            this.moveTween.stop();
            this.moveTween = null;
        }
        this.moveTween = cc.tween(this.node).to(0.1,{position: cc.v3(info.x, this.node.y, info.z)}).call(() => { this.playIdle(); this.moveTween = null; });
        this.moveTween.start();
    },

    update: function(delta){
        if(this.node.zIndex != Math.floor(this.node.z)){
            this.node.zIndex = Math.floor(this.node.z);
        }
    }
});