let Server = require("Server");
let PlayerData = require("PlayerData");

let PlayerBase = cc.Class({
    extends: cc.Component,

    properties:{
       
        server: Server,
        anim: cc.Animation,
        playerData: PlayerData  = null,
        signLabel: cc.Label
    }, 

    statics:{
        ANIM_IDLE: "nft_0_idle",
        ANIM_WALK:"nft_0_walk"
    },

    onLoad: function(){
        this.playIdle();     
    },

    setPlayerData: function(value){
        this.playerData = value;
    },

    getPlayerData: function(){
        return this.playerData;
    },

    updateSignLabel: function(){
        if(this.playerData.getSignText() != null){
            this.signLabel.string = this.playerData.getSignText();
        }
    },

    updateSignScale: function(scale){
        this.signLabel.node.scaleX = scale;
    },

    playIdle: function(){
        if(this.anim.currentClip && this.anim.currentClip.name == PlayerBase.ANIM_IDLE){

            return;
        }
        this.anim.play(PlayerBase.ANIM_IDLE);
    },

    playWalk: function(){
        if(this.anim.currentClip && this.anim.currentClip.name == PlayerBase.ANIM_WALK){

            return;
        }
        this.anim.play(PlayerBase.ANIM_WALK);
    },


    
});