let MyPlayer = require("MyPlayer");

let World = cc.Class({
    extends: cc.Component,

    properties:{
        bgLayer: cc.Node,
        cameras: [cc.Camera],
        myPlayer: cc.Node,

        isKeyA:false,
        isKeyD:false,
        isKeyW:false,
        isKeyS:false,

        isKeyO:false,
        isKeyP:false,

        isK:false,
        isL:false
       
    },

    statics:{
        WORLD_CAMERA_IND: 1,
        WORLD_BORDER_MIN_X: -1700,
        WORLD_BORDER_MAX_X: 1700,
        BG_SPEED_RATIO: 0.72
    },

    onLoad: function(){
        console.log(cc.Canvas.instance.node.getContentSize().width);

       // cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
       // cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
    },

    onDestroy: function(){
       // cc.systemEvent.off(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
       // cc.systemEvent.off(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
    },

    update: function(){
        let cameraPox = this.myPlayer.x;
        cameraPox = Math.max(cameraPox, World.WORLD_BORDER_MIN_X + cc.Canvas.instance.node.getContentSize().width/2);
        cameraPox = Math.min(cameraPox,  World.WORLD_BORDER_MAX_X - cc.Canvas.instance.node.getContentSize().width/2);
        for(let i = 0; i < this.cameras.length; i ++){
           this.cameras[i].node.x = cameraPox;
        }

        if(this.isKeyA){
            for(let i = 0; i < this.cameras.length; i ++){
                this.cameras[i].node.z +=1;
             }
        }

        if(this.isKeyD){
            for(let i = 0; i < this.cameras.length; i ++){
                this.cameras[i].node.z -=1;
             }
        }

        if(this.isKeyW){
            for(let i = 0; i < this.cameras.length; i ++){
                this.cameras[i].node.y +=1;
             }
        }

        if(this.isKeyS){
            for(let i = 0; i < this.cameras.length; i ++){
                this.cameras[i].node.y -=1;
             }
        }

        if(this.isKeyO){
            for(let i = 0; i < this.cameras.length; i ++){
                this.cameras[i].node.eulerAngles.x +=0.1;
             }
        }

        if(this.isKeyP){
            for(let i = 0; i < this.cameras.length; i ++){
                this.cameras[i].node.eulerAngles.x -=0.1;
             }
        }

        if(this.isK){
            for(let i = 0; i < this.cameras.length; i ++){
                this.cameras[i].fov +=0.1;
             }
        }

        if(this.isL){
            for(let i = 0; i < this.cameras.length; i ++){
                this.cameras[i].fov -=0.1;
             }
        }
    },

    onKeyDown: function (event) {
        switch(event.keyCode) {
            case cc.macro.KEY.a:
                this.isKeyA = true;
                break;
            case cc.macro.KEY.d:
                this.isKeyD = true;
                break;
            case cc.macro.KEY.w:
                this.isKeyW = true;
                break;
            case cc.macro.KEY.s:
                this.isKeyS = true;
                break;
            case cc.macro.KEY.o:
                this.isKeyO = true;
                break;
            case cc.macro.KEY.p:
                this.isKeyP = true;
                break;
            case cc.macro.KEY.k:
                this.isK= true;
                break;
            case cc.macro.KEY.l:
                this.isL = true;
                break;
        }
    },

    onKeyUp: function (event) {
        switch(event.keyCode) {
            case cc.macro.KEY.a:
                this.isKeyA = false;
                break;
            case cc.macro.KEY.d:
                this.isKeyD = false;
                break;
            case cc.macro.KEY.w:
                this.isKeyW = false;
                break;
            case cc.macro.KEY.s:
                this.isKeyS = false;
                break;
            case cc.macro.KEY.o:
                this.isKeyO = false;
                break;
            case cc.macro.KEY.p:
                this.isKeyP = false;
                break;
            case cc.macro.KEY.k:
                this.isK= false;
                break;
            case cc.macro.KEY.l:
                this.isL = false;
                break;
            case cc.macro.KEY.u:
                console.log(this.cameras[0].node.postion, this.cameras[0].node.eulerAngles,this.cameras[0].fov);
                break;
        }
    }
});