let Server = require("Server");
let World  = require("World");
let PlayerBase = require("PlayerBase");
let PlatformTouch = require("PlatformTouch");
let Helper = require('Helper');
const { PlayerData } = require("./PlayerData");

let MyPlayer = cc.Class({
    extends: PlayerBase,

    properties:{
        isPositionDirty: false, 
        ax: 0, 
        speedx: 0, 
        updateTimer: 0, 
        world: World,
        setTouch: false, 
        moveTween: null
    },
    

    statics:{
        SPEED_AX : 1,
        MAX_SPEED: 5, 
        SEVER_UPDATE_INTERVAL:0.1
    },

    onLoad: function(){
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
  
        //set Z
        this.node.z = this.node.zIndex = Math.floor(Math.random() * 10 - 5) *10;
        console.log("MyPlayer", this.node.z);
        

        this.setPlayerData(new PlayerData());
        this.playerData.setSignText(Helper.createSign);
        this.updateSignLabel();

        //this.server.sendSpawn({pos:this.node.getPosition(), sign:Helper.createSign});
    },

    sendSpawn(){
        this.server.sendSpawn({pos:this.node.getPosition(), sign:Helper.createSign});
    },

    onDestroy: function(){
        cc.systemEvent.off(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        cc.systemEvent.off(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
    },

    onKeyDown: function (event) {
        switch(event.keyCode) {
            case cc.macro.KEY.dpadLeft:
            case cc.macro.KEY.left:
                console.log('Press a left key');
                this.moveLeft();
                break;
  
            case cc.macro.KEY.dpadRight:
            case cc.macro.KEY.right:
                console.log('Press a right key');
                this.moveRight();
                break;
        }
    },
  
    onKeyUp: function (event) {
        switch(event.keyCode) {
            case cc.macro.KEY.dpadLeft:
            case cc.macro.KEY.left:
            case cc.macro.KEY.dpadRight:
            case cc.macro.KEY.right:
              this.stopMove();
                break;
        }
    },

    moveLeft: function(){
        if(this.moveTween){
            this.moveTween.stop();
            this.moveTween = null;
        }
        this.ax = - MyPlayer.SPEED_AX;
        this.anim.node.scaleX = -1;
        this.playWalk();
        this.stop = false;
    },

    moveRight: function(){
        if(this.moveTween){
            this.moveTween.stop();
            this.moveTween = null;
        }
        this.ax =  MyPlayer.SPEED_AX;
        this.anim.node.scaleX = 1;
        this.playWalk();
        this.stop = false;
    },

    stopMove: function(){
        if(this.speed > 0){
            this.ax = - MyPlayer.SPEED_AX;
        }
        else if(this.speed < 0){
            this.ax = MyPlayer.SPEED_AX;
        }
        else{
            this.ax = 0;
            this.playIdle();
        }
        this.stop = true;
    },

    update: function(delta){
        if(!this.setTouch && PlatformTouch.instance != null){
            this.setTouch = true;
            PlatformTouch.instance.node.on("click_on_ground", this.clickToWalk.bind(this));
        }
        if(this.ax != 0){
           
            if(this.speedx * this.ax < 0 && Math.abs(this.speedx) <=  Math.abs(this.ax) && this.stop){
                //stopped
                this.speedx = this.ax = 0;
                this.playIdle();
            }
            else{
                this.speedx += this.ax;
                this.speedx = this.speedx > 0 ? Math.min(MyPlayer.MAX_SPEED , this.speedx) : Math.max( - MyPlayer.MAX_SPEED, this.speedx);

                let oldx = this.node.x;

                this.node.x += this.speedx;
                this.node.x = Math.min(World.WORLD_BORDER_MAX_X, this.node.x);
                this.node.x = Math.max(World.WORLD_BORDER_MIN_X, this.node.x);
                if(oldx != this.node.x){
                    this.isPositionDirty = true;
                }
               
            }
          
        }
        if(this.node.zIndex != Math.floor(this.node.z)){
            this.node.zIndex = Math.floor(this.node.z);
        }

        if(this.moveTween){
            this.isPositionDirty = true;
        }

        this.updateTimer += delta;
        if(this.updateTimer > MyPlayer.SEVER_UPDATE_INTERVAL && this.isPositionDirty){
            this.updateTimer = this.updateTimer %  MyPlayer.SEVER_UPDATE_INTERVAL;
            this.isPositionDirty = false;
            this.server.sendPosition(this.node.getPosition());
        }
    },

    clickToWalk: function(worldPos){
        //pos =  this.node.convertToNodeSpaceAR(cc.Vec3(pos.x, pos.y, pos.z));
        let pos = (this.node.parent.convertToNodeSpaceAR(cc.Vec3( worldPos.x,  worldPos.y,  worldPos.z)));
        console.log(pos.x, pos.y, pos.z, Math.floor(pos.z));
        this.ax = 0;
        this.speedx = 0;

        this.node.stopAllActions();
        this.playWalk();

        let oldx = this.node.x;
        if(pos.x > oldx){
            this.anim.node.scaleX = 1;
            this.updateSignScale(1);
        }
        else if(pos.x < oldx){
            this.anim.node.scaleX = -1;
            this.updateSignScale(-1);
        }

       /* this.node.runAction(cc.sequence(cc.moveTo(Math.max(0.1,Math.abs(pos.x - oldx)/100), cc.Vec3(pos.x, this.node.y, Math.floor(pos.z))), cc.callFunc(function(){
            this.playIdle();
            console.log(this.node.z);
        }.bind(this))));*/
        if(this.moveTween){
            this.moveTween.stop();
            this.moveTween = null;
        }
        this.moveTween = cc.tween(this.node).to(Math.max(0.1,Math.abs(pos.x - oldx)/100),{position: cc.v3(pos.x, this.node.y, Math.floor(pos.z))}).call(() => { this.playIdle(); 
            console.log(this.node.position);
            this.moveTween = null; });
        this.moveTween.start();

    }


});
