let OtherPlayer = require("OtherPlayer");
let MyPlayer = require("MyPlayer");
const { PlayerData } = require("./PlayerData");

cc.Class({
    extends: cc.Component,

    properties:{
      player: MyPlayer,
      otherPlayers: null,

      otherPlayerPrefab: cc.Prefab,
      playersContainer: cc.Node,

    },

    onLoad(){
      this.otherPlayers = {};
       
     
    },

    onDestroy(){
     
    },

  update: function(){
    
  },

  onConnectedHandler: function(){
    this.player.sendSpawn();
  },

  getMyPlayer: function(){
    return this.player;
  },

  addAPlayer: function(id){
    let player = cc.instantiate(this.otherPlayerPrefab);
    player.parent = this.playersContainer;
    let otherPlayer = player.getComponent("OtherPlayer");

    let playerData = new PlayerData();
    playerData.setSocketID(id);
    otherPlayer.setPlayerData(playerData);

    this.otherPlayers[id] = otherPlayer
    return otherPlayer;
  },

  getAPlayer: function(id){
    return this.otherPlayers[id];
  }, 

  removeAPlayer: function(id){
    let player =  this.otherPlayers[id] ;
    if(player){
      player.node.destroy();
    }
  }, 
  
})